export const UNPROCESSABLE = 422
export const ZERO_STR = "0"

export const FIXED_CURRENCY_NETWORK = {
    "BTC": "Bitcoin", "USDT": "BNB Smart Chain", "USDC": "Ethereum", "ETH": "Ethereum",
}
export const sleep = (delay) => new Promise((resolve) => setTimeout(resolve, delay))

export function getUuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = (Math.random() * 16) | 0, v = c === 'x' ? r : (r & 0x3) | 0x8;
        return v.toString(16);
    });
}

/**
 *
 * @param url
 * @param body
 * @returns {Promise<{success: boolean, json: JSON, text: string, status: number}>}
 */
export async function post(url, body) {
    return await doReq(url, "POST", body)

}

/**
 *
 * @param url
 * @returns {Promise<{success: boolean, json: JSON, text: string, status: number}>}
 */
export async function get(url) {
    return await doReq(url, "GET", null)
}

async function doReq(url, method, body) {
    let init = {
        method: method,
        headers: {
            "content-type": "application/json",
            "X-Csrf-Token": getXsrfToken(),
            "my-email": window.Noones?.currentUser?.email,
        },
        body: body == null ? null : typeof body === 'string' ? body : JSON.stringify(body),
        redirect: "manual",
        mode: "cors",
        credentials: "include"
    };
    let response = await fetch(url, init);
    let text = await response.text();

    let res = {
        status: response.status, success: 200 === response.status, text: text, json: null
    }
    if (text && (text.trimStart().startsWith("{") || text.trimStart().startsWith("["))) {
        res.json = JSON.parse(text)
    }
    logReq(init, url, res);
    return res
}

function logReq(init, url, res) {
    console.log("=========== request init ===========")
    init.url = url
    console.log(init)
    console.log("=========== response ===========")
    res.url = url
    if (res.success) {
        console.log(res)
    } else {
        console.error(res)
    }
}

export function getXsrfToken() {
    let name = "XSRF-TOKEN=";
    let ca = document.cookie.split(';');
    for (let i = 0; i < ca.length; i++) {
        let c = ca[i].trim();
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length);
    }
    return "";
}



